<template lang="pug">
.fl
	.hello
		.zag Атрибуты
		.item(v-for="( item, index ) in attributes" :key="index" contenteditable)
			span {{ item.label }}
			|{{ item.value }}
		.item1
			.lab Исполнители:
			.rela
				.bl(@click="editMode" v-click-outside="normalMode" :class="mode ? 'expanded' : ''")
					transition(name="list" mode="out-in")
						.hid(v-if="mode")
							form(@submit.prevent="addNewItem()")
								img(:src="require(`@/assets/sprav.svg`)" @click="openModal").sprav
								.add
									v-autocomplete( v-model="user" :items="users" autofocus :filter="customFilter" item-text="name" clearable )
										template( v-slot:item="data" )
											v-list-tile-avatar
												img(:src="require(`@/assets/user0.svg`)").my
											v-list-tile-content
												v-list-tile-title.tit {{ data.item.name}}
												<!-- v-list-tile-title.tit {{ data.item }} -->
												v-list-tile-sub-title.subt отдел, должность

					transition-group(name="list" tag="ul")
						li.fio(v-for="(item, index) in people" :key="item.name")
							.close(v-if="mode" @click="deleteElement(index)") &times;
							img(:src="require(`@/assets/${item.icon}`)" v-if="item.icon").icon
							|{{ item.name }}
		.item
			span Номер:
			| №123-45-21

	.content 

	v-dialog( v-model="modal" width="500" persistent )
		v-card
			v-card-title( primary-title ).headline.grey.lighten-2 Здесь справочник
			v-card-text
				p Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt mollit anim id est laborum.
			v-divider

			v-card-actions
				v-spacer
				v-btn( color="primary" flat @click="closeModal" ) Закрыть

</template>

<script>
import ClickOutside from 'vue-click-outside'
import users from './users.json'

export default {
	data () {
		return {
			mode: false,
			modal: false,
			users: users,
			user: null,
			attributes: [
				{ label: 'Вид:', value: 'На исполнение' },
				{ label: 'Статус:', value: 'Не начато' },
				{ label: 'Создано:', value: '20.05.19  16:04' },
				{ label: 'Срок:', value: '25.05.19' },
				{ label: 'Автор:', value: 'Петров Г.А.' }
			],
			people: [
				{ id: 0, name: 'Орлов В.В.', icon: '' },
				{ id: 1, name: 'Администратор', icon: 'role.svg' },
				{ id: 2, name: 'Воронцов Н.Н.', icon: '' },
				{ id: 3, name: 'Журавлева П.А.', icon: '' },
				{ id: 4, name: 'ООО "Ромашка"', icon: 'organization.svg' },
				{ id: 5, name: 'Консолидатор', icon: 'search-word.svg' },
				{ id: 6, name: 'Бухгалтерия', icon: 'department.svg' },
				{ id: 7, name: 'Заместители', icon: 'group.svg' }
			]
		}
	},
	methods: {
		openModal () {
			this.modal = true
		},
		closeModal () {
			this.modal = false
			this.mode = true
		},
		editMode () {
			this.mode = true
		},
		normalMode () {
			this.mode = false
			this.user = ''
		},
		deleteElement (e) {
			this.people.splice(e, 1)
		},
		addNewItem () {
			if (this.user === '') return
			this.people.push({
				name: this.user
			})
			this.user = ''
		},
		customFilter (item, queryText, itemText) {
			const textOne = item.name.toLowerCase()
			const searchText = queryText.toLowerCase()
			
			return textOne.indexOf(searchText) > -1
    },
	},
	directives: {
		ClickOutside
	}
}

</script>

